.login {
    width: 100%;
    height: 100vh;
    display: block;
    background: white;

}

.loginGrid {
    display: grid;
    grid-template-columns: 70% 30%;
}

.loginCard {
    width: 100%;
    height: 100vh;
    padding: 0;
}

.loginCard h3 {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1rem;
}

.loginCard h5 {
    text-align: center;
    font-size: 1.5rem;
}

.loginCard span {
    color: rgba(150, 96, 242, 1)
}

.bg {
    background: url('../assets/bg.jpg');
    background-size: cover;
    object-fit: contain;
    background-position: center;
}

.loginPadding {
    padding: 10rem 3rem;
}


/* Forms */
.inputContainer {
    width: 100%;
    height: auto;
    display: block;
    margin-bottom: 1rem;
}

.formTopPadding {
    padding-top: 5rem;
}

.inputContainer input,
select,
textarea,
button {
    width: 100%;
    height: 4rem;
    padding: .5rem 2rem;
    display: block;
    border: 1px solid gainsboro;
    border-radius: 5px;
    outline: none;
    background: gainsboro;
}

.inputContainer input:hover {
    outline: none;
}

.btn {
    width: 100%;
    height: 4rem;
    padding: .5rem 2rem;
    display: flex;
    border-radius: 5px;
    background: gainsboro;
    text-align: center;
    align-items: center;
    justify-content: center;
    transition: 1s ease-in-out;
}

.btn:hover {
    transform: scale(1.1);
}

.purple {
    background: linear-gradient(90deg, rgba(196, 94, 213, 1), rgba(150, 96, 242, 1));
    color: white;
}

button svg {
    float: right;
    font-size: 1.5rem;
    margin-right: 1rem;
}

.right {
    text-align: right;
    float: right;
    margin-bottom: 1rem;
}