.page {
    width: 87%;
    min-height: 100vh;
    height: auto;
    display: block;
    margin-left: 13%;
}

.contentPadding {
    padding: 2rem;
}

.cardGrid {
    display: grid;
    grid-template-columns: 23% 23% 23% 23%;
    grid-gap: 2%;
}

.chartGrid {
    display: grid;
    grid-template-columns: 48% 48%;
    grid-gap: 2%;
}

.profileGrid {
    display: grid;
    grid-template-columns: 18% 80%;
    grid-gap: 2%;
}

.threeGrid {
    display: grid;
    grid-template-columns: 31% 31% 31%;
    grid-gap: 2%;
}

.contents {
    width: 100%;
    height: auto;
    display: block;
}

.profile {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin: 0 auto;
}

.profile img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
}




/* colors */
.red {
    background: linear-gradient(0deg, rgba(254, 101, 61, 1), rgba(244, 69, 92, 1));
    color: white;
}

.blue {
    background: linear-gradient(0deg, rgba(24, 181, 243, 1), rgba(24, 126, 243, 1));
    color: white;
}

.green {
    background: linear-gradient(0deg, rgba(28, 238, 122, 1), rgba(66, 193, 101, 1));
    color: white;
}

.purple {
    background: linear-gradient(0deg, rgba(196, 94, 213, 1), rgba(150, 96, 242, 1));
    color: white;
}

.dpurple {
    background: #F2EBFB;
    color: #713BDB;
}

.cyan {
    background: #EEF9FF;
    color: #49B8FF;
}

.gold {
    background: #FEF3EF;
    color: #FE8A57;
}

.crimson {
    background: #FFEFF2;
    color: #FD5181;
}


/* Forms */
.rows {
    display: grid;
    grid-template-columns: 48% 48%;
    grid-gap: 2%;
}

.formPadding {
    padding: 1rem 5rem;
}

.inputContainer {
    width: 100%;
    height: auto;
    display: block;
    margin-bottom: 1rem;
}

.inputContainer input,
select,
textarea,
button {
    width: 100%;
    height: 3rem;
    padding: .5rem 2rem;
    display: block;
    border: 1px solid gainsboro;
    border-radius: 5px;
    outline: none;
    background: none;
}

.inputContainer input:hover {
    outline: none;
}