.navigation {
    width: 13%;
    height: 100vh;
    position: fixed;
    background: white;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
}

.logo {
    width: 100%;
    height: 5rem;
    margin-bottom: 2rem;
}

.logo img {
    object-fit: contain;
    max-height: 5rem;
    margin: 0 auto;
}

.url {
    width: 100%;
    height: auto;
    display: block;

}

.url ul {
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
}

.url ul li {
    width: 100%;
    padding: 0;
    margin: 0;
    height: 3rem;
    line-height: 3rem;
    padding-left: 1rem;
    cursor: pointer;
}

.url ul li a {
    text-decoration: none;
    color: dimgray;
    font-size: 13px;
    float: left;
}

.url ul li span {
    text-decoration: none;
    color: dimgray;
    font-size: 13px;
    float: left;
}

.url ul li a svg {
    float: left;
    font-size: 20px;
    margin-top: .8rem;
    margin-right: .5rem;
}

.url ul li span svg {
    float: left;
    font-size: 20px;
    margin-top: .8rem;
    margin-right: .5rem;
}

.hide {
    display: none;
}

.show {
    width: 100%;
    height: auto;
    display: block;
    background: linear-gradient(90deg, rgba(12, 28, 82, 0.8), rgba(12, 28, 82, 0.8));

}

.show ul li {
    padding-left: 1rem;
}

.show ul li a {
    font-size: 12px;
    color: white;
}