.card {
    width: 100%;
    height: 7rem;
    overflow: hidden;
    background: white;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    border-radius: 10px;
    margin-bottom: 1rem;
}

.grid {
    display: grid;
    grid-template-columns: 25% 75%;
}

.icon {
    width: 100%;
    height: 7rem;
    padding: .5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.square {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
}

.square svg {
    margin: 0 auto;
}

.text {
    width: 100%;
    height: 7rem;
    padding: 1rem 2rem;
}

.text h1 {
    font-size: 2rem;
    font-weight: bold;
    color: rgb(2, 9, 46);
}

.text p {
    font-size: .8rem;
}