.cards {
    width: 100%;
    height: 10rem;
    display: block;
    border-radius: 10px;
}

.header {
    width: 100%;
    height: 3rem;
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    border-bottom: 1px solid gainsboro;
}

.content {
    width: 100%;
    height: 7rem;
    padding: 2rem;
}

.content h1 {
    font-weight: bold;
    font-size: 2rem;
}