.header {
    width: 100%;
    height: 4rem;
    border-bottom: 1px solid whitesmoke;
    padding: .8rem 2rem;
}

.flex {
    display: flex;
    justify-content: space-between;
}

.left {
    display: flex;
    align-items: center;
}

.right {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.right p {
    margin-right: 1rem;
}